import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'displayDate'
})
export class DisplayDatePipe implements PipeTransform {

  transform(dateobject: Date): any {
    if(!dateobject)
        return '';
    else
        return dateobject.getDate() + '/' + (dateobject.getMonth()+1) + '/' + dateobject.getFullYear();
  }

}
