import { Component, Input, Output, EventEmitter } from '@angular/core';

import { HttpService } from 'src/app/shared/services/http-service.service';
import { UtilityService } from 'src/app/shared/services/utility.service';
import { SearchEmployeeComponent } from 'src/app/shared/components/searchEmployee/searchEmployee.component';

declare var context;

@Component({
  selector: 'app-update-team',
  templateUrl: './updateTeam.component.html',
  styleUrls: ['./updateTeam.component.scss']
})
export class UpdateTeamComponent {

  @Input() team;

  @Output() saveTeam = new EventEmitter<any>();
  @Output() closeTeamEdit = new EventEmitter<any>();

  popupWidth: string;
  
  constructor(private httpService: HttpService,
              private utilityService: UtilityService){
    
  }

  ngOnInit(){
    if(this.utilityService.getResponsivenessStatus()){
      this.popupWidth = "80%";
    }
    else{
      this.popupWidth = "350px";
    }
  }

  saveProject(){
    this.saveTeam.emit(this.team);
  }

  closeEdit(){
    this.closeTeamEdit.emit();
  }

  selectedEmployee(employee){
    console.log(employee);
    this.team.managers.push({
      employeeName: employee.name,
      employeeId: employee.eid,
      empRole: employee.employeeRoles[0].roleName,
    });
  }

  removeManager(index){
    this.team.managers.splice(index, 1);
  }

}

