import { Component, Input, Output, EventEmitter } from '@angular/core';

import { environment } from 'src/environments/environment';
import { HttpService } from 'src/app/shared/services/http-service.service';
import { UtilityService } from 'src/app/shared/services/utility.service'

declare var context;

@Component({
  selector: 'mis-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent {

  teams: any = [];
  selectedTeam: any;
  showTeamEdit: boolean = false;
  showTeamSchedule: boolean = false;
  isMobileView: boolean = false;
  showLoader: boolean = false;
  imageURL: string = "";
  
  constructor(private httpService: HttpService,
              private utilityService: UtilityService){

  }

  ngOnInit(){
    this.imageURL = environment.imageEndPoint;
    this.isMobileView = this.utilityService.getResponsivenessStatus();
    this.getTeams();
  }

  getTeams(){
    this.showLoader = true;
    this.httpService.getTeams('').subscribe( res => {
      this.showLoader = false;
      if(res)
        this.teams = res;
    }, err => {
      this.showLoader = false;
    });
  }

  saveTeam(team){
    this.showLoader = true;
    this.httpService.saveTeam(team).subscribe(res => {
      this.showLoader = false;
      this.closeTeamEdit();
      this.getTeams();
    }, err => {
      this.showLoader = false;
      console.log("Error occured while saving the project");
    });
  }

  editTeam(index){
    this.selectedTeam = this.teams[index];
    this.showTeamEdit = true;
  }

  closeTeamEdit(){
    this.showTeamEdit = false;
  }

  scheduleTeam(index){
    this.selectedTeam = this.teams[index];
    this.showTeamSchedule = true;
  }

  closeScheduleTeam(){
    this.showTeamSchedule = false;
  }


}

