import { Component, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { mobiscroll, MbscCalendarOptions } from '@mobiscroll/angular';

import { LoaderComponent } from 'src/app/shared/components/loader/loader.component';
import { environment } from 'src/environments/environment';
import { HttpService } from 'src/app/shared/services/http-service.service';
import { UtilityService } from 'src/app/shared/services/utility.service';
import { TmsDataService } from 'src/app/shared/services/tms-data.service';

mobiscroll.settings = {
  theme: 'ios',
  display: 'bubble'
}; 

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.scss']
})
export class ScheduleComponent {

  @Input() team;

  teams: any = [];
  isMobileView: boolean = false;
  maxSettings: MbscCalendarOptions;

  imageURL: string;
  
  scheduleEditType: string;
  selectedSchedule: any;
  selectedDateIndex;
  scheduleEmployeeId: string;

  selectedEmpList: any = [];

  schedules: any = [];
  scheduleStartDate: string;
  scheduleEndDate: string;
  scheduleDateRangeArr = [];
  showScheduleEdit: boolean = false;
  showLoader: boolean = false;
  
  constructor(private httpService: HttpService,
              private utilityService: UtilityService,
              private dataService: TmsDataService, 
              private cdr: ChangeDetectorRef){
      
  }

  ngOnInit(){
    this.imageURL = environment.imageEndPoint;
    this.isMobileView = this.utilityService.getResponsivenessStatus();

    let that = this;
    let today = new Date(), nextDate = new Date();
    nextDate.setDate(today.getDate() + 30);
    this.maxSettings = {
      display: 'bubble',
      showOnTap: true,
      weekDays: 'min',
      firstDay: 1,
      selectType: (that.isMobileView ? 'day': 'week'),
      select: (that.isMobileView ? 1 : 7),
      max: nextDate,
      headerText: '',
      dateFormat: 'dd/mm/yy',
      onSet: (event) => {
        if(event && event['valueText']){
          let selDates =  event.valueText.split(',');
          this.scheduleStartDate = selDates[0].trim();
          this.scheduleEndDate = selDates[selDates.length-1].trim();
          this.scheduleDateRangeArr = selDates.map( date => {
            return that.utilityService.getDateFromString(date.trim());
          });;
          this.getSchedules();
        }
      },
      onInit: function (event, inst) {
        let dateObjArr = [];
        let maxSelection = (that.isMobileView ? 1 : 7);
        today.setHours(0); today.setMinutes(0); today.setSeconds(0); today.setMilliseconds(0);
        today.setDate(today.getDate() - today.getDay() + 1);
        for(let i=0; i<maxSelection; i++){
          dateObjArr.push(new Date(today));
          today.setDate(today.getDate()+1);
        }
        that.scheduleStartDate = that.utilityService.getStringDate(dateObjArr[0]);
        that.scheduleEndDate = that.utilityService.getStringDate(dateObjArr[dateObjArr.length-1]);
        that.scheduleDateRangeArr = dateObjArr;
        inst.setVal(dateObjArr, true);
        that.getSchedules();
      }
    };
    this.httpService.getOffices().subscribe(res => {
      if(res)
        this.dataService.returnOffices.next(res);
    });
  }

  getSchedules(){
    if(this.team){
      this.showLoader = true;
      this.cdr.detectChanges();
      this.httpService.getSchedulesForATeam(this.team.projectId, this.scheduleStartDate, this.scheduleEndDate).subscribe(
        res => {
          this.showLoader = false;
          this.cdr.detectChanges();
          if(res)
            this.schedules = res;
      }, err => {
        this.showLoader = false;
        this.cdr.detectChanges();
      });
    }
  }

  editSchedule(type, schedules, index){
    let scheduleDate = new Date(this.scheduleDateRangeArr[index]); scheduleDate.setHours(0,0,0,0);
    this.selectedDateIndex = index;
    this.scheduleEditType = type;
    if(type != 'BOTH')
      this.scheduleEmployeeId = schedules.employeeId;
    if(schedules)
      this.selectedSchedule = this.getSchedule(schedules, index, 'Object', type);
    this.showScheduleEdit = true;
  }

  updateSchedule(scheduleObj){
    this.showScheduleEdit = false;
    this.showLoader = true;
    this.cdr.detectChanges();
    //Checking if it is a bulk edit or single edit
    if(this.scheduleEditType === 'BOTH'){
      scheduleObj['empGuidList'] = this.selectedEmpList;
      this.httpService.updateSchedulesBulk(scheduleObj.projectId, scheduleObj).subscribe(res => {
        this.showLoader = false;
        this.cdr.detectChanges();
        this.getSchedules();
      }, err => {
        this.showLoader = false;
        this.cdr.detectChanges();
      });
    }
    else{
      this.httpService.updateSchedule(scheduleObj.empGuid, scheduleObj.projectId, scheduleObj.schedules).subscribe(res => {
        this.showLoader = false;
        this.cdr.detectChanges();
        this.getSchedules();
      }, err => {
        this.showLoader = false;
        this.cdr.detectChanges();
      });
    }
  }

  cancelSchedule(type, schedule, index){
    let scheduleDate = new Date(this.scheduleDateRangeArr[index]); scheduleDate.setHours(0,0,0,0);
    let selectedShift = this.getSchedule(schedule, index, 'Object', type);
    let requestObj = {};
    requestObj['adhoc'] = false;
    requestObj['empGuid'] = schedule.employeeId;
    requestObj['gender'] = 'male';
    requestObj['hours'] = selectedShift.shiftParameters['hours'];
    requestObj['minutes'] = selectedShift.shiftParameters['minutes'];
    requestObj['projectId'] = this.team.projectId;
    requestObj['scheduleDate'] = scheduleDate.getTime();
    requestObj['scheduleRequestType'] = type;
    this.showLoader = true;
    this.httpService.cancelSchedule(schedule.employeeId, this.team.projectId, requestObj).subscribe(res => {
      this.showLoader = false;
      this.getSchedules();
    }, err => {
      this.showLoader = false;
    });
  }

  closeScheduleEdit(){
    this.showScheduleEdit = false;
  }

  getSchedule(scheduleEle, dateIndex, requiredType, scheduleType){
    let scheduleDate = new Date(this.scheduleDateRangeArr[dateIndex]); scheduleDate.setHours(0,0,0,0);
    let schedule = scheduleEle.sheduleMapForEmployee[scheduleDate.getTime()]; 
    let requiredSchedule;
    if(schedule){
      for(let i=0; i<schedule.length; i++){
        if(schedule[i].eventName == scheduleType){
          requiredSchedule = schedule[i];
          break;
        }
      }
    }
    if(requiredSchedule){
      if(requiredType == 'Object')
        return requiredSchedule;
      else
      {
        let hour = '0' + requiredSchedule.shiftParameters['hours'];
        let minute = '0' + requiredSchedule.shiftParameters['minutes'];
        return hour.substring(hour.length-2, hour.length) + ':' + minute.substring(minute.length-2, minute.length);
      }
    }
    else{
      if(requiredType == 'Object')
        return {};
      else
        return '--:--';
    }
  }

  selectAllEmployees(event){
    if(event.srcElement.checked){
      this.selectedEmpList = [];
      this.schedules.forEach(schedule => {
        this.selectedEmpList.push(schedule.employeeId);
      });
    }
    else{
      this.selectedEmpList = [];
    }
  }

  selectEmployee(empGuid){
    if(this.selectedEmpList.includes(empGuid))
      this.selectedEmpList.splice(this.selectedEmpList.indexOf(empGuid), 1);
    else
      this.selectedEmpList.push(empGuid);
  }

  isGreyedOut(date: Date, schedule){
    if(schedule && schedule.scheduleExceptionForEmployee &&
      Object.keys(schedule.scheduleExceptionForEmployee).includes(date.getTime().toString())
    )
    return true;
    else
    return false;
  }
}

