import { Component, Input, Output, EventEmitter } from '@angular/core';
import { mobiscroll, MbscCalendarOptions } from '@mobiscroll/angular';

import { HttpService } from 'src/app/shared/services/http-service.service';
import { UtilityService } from 'src/app/shared/services/utility.service';
import { TmsDataService } from 'src/app/shared/services/tms-data.service';
;
mobiscroll.settings = {
  theme: 'ios',
  display: 'bubble'
};

@Component({
  selector: 'app-update-schedule',
  templateUrl: './updateSchedule.component.html',
  styleUrls: ['./updateSchedule.component.scss']
})
export class UpdateScheduleComponent {

  @Input() schedule;
  @Input() type;
  @Input() team;
  @Input() date;
  @Input() employeeId;

  @Output() close = new EventEmitter<any>();
  @Output() update = new EventEmitter<any>();

  maxSettings: MbscCalendarOptions;
  popupWidth: string;
  offices: any = [];
  loginShifts;
  logoutShifts;
  selectedLoginShift;
  selectedLogoutShift;
  selectedLoginOffice = {};
  selectedLogoutOffice =  {};
  isSameDayLogout: boolean = false;

  selectedDates;
  
  constructor(private httpService: HttpService,
              private utilityService: UtilityService,
              private dataService: TmsDataService){
    
  }

  ngOnInit(){
    if(this.utilityService.getResponsivenessStatus())
      this.popupWidth = "80%";
    else this.popupWidth = "";
    let that = this;
    let today = new Date(), nextDate = new Date();
    nextDate.setDate(today.getDate() + 30);
    this.maxSettings = {
      display: 'bubble',
      showOnTap: true,
      weekDays: 'min',
      select: 7,
      min: today,
      max: nextDate,
      headerText: '',
      dateFormat: 'dd/mm/yy',
      onSet: (event) => {
        if(event && event['valueText']){
          let selDate =  event.valueText.split(',');
          this.selectedDates = selDate.map( date => {
            return that.utilityService.getDateFromString(date.trim()).getTime();
          });
        }
      },
      onInit: function (event, inst) {
        let dateObjArr = [];
        for(let i=0; i<1; i++){
          dateObjArr.push(new Date(today));
          today.setDate(today.getDate()+1);
        }
        inst.setVal(dateObjArr, true);
      }
    };
    this.dataService.returnOffices.subscribe(offices => {
      if(offices){
        this.offices = offices;
        this.selectedLoginOffice =  this.offices[0];
        this.selectedLogoutOffice =  this.offices[0];
        if(this.type == 'BOTH')
          this.date = new Date();
        this.getShifts(this.selectedLoginOffice);
      }
    });
    if(this.type == 'BOTH')
      this.date = new Date();
  }

  getShifts(office){
    this.httpService.getShifts(this.team.projectId, this.utilityService.getStringDate(this.date),
                  this.utilityService.getStringDate(this.date), office['address'] ).subscribe(
        res => {
          let key1 = Object.keys(res)[0];
          let requiredShifts = res[key1];
          this.loginShifts = []; this.logoutShifts = [];
          requiredShifts.forEach(shift => {
              if(shift.shiftType == 'LOGIN')
                this.loginShifts.push(shift);
              if(shift.shiftType == 'LOGOUT')
                this.logoutShifts.push(shift);
              if(this.type!= 'BOTH' && this.schedule && this.schedule.shiftParameters['hours'] == shift.hours &&
                  this.schedule.shiftParameters['minutes'] == shift.minutes){
                  if(this.type === 'LOGIN')
                    this.selectedLoginShift = shift;
                  else if(this.type === 'LOGOUT')
                    this.selectedLogoutShift = shift;
              }
          });
        }, err => {
          console.log("Error in loading the shifts");
        });
  }

  selectLoginShift(shift){
    this.selectedLoginShift = shift;
  }

  selectLogoutShift(shift){
    this.selectedLogoutShift = shift;
  }

  selectLoginOffice(office){
    this.selectedLoginOffice = office;
    this.getShifts(this.selectedLoginOffice);
  }

  selectLogoutOffice(office){
    this.selectedLogoutOffice = office;
    this.getShifts(this.selectedLogoutOffice);
  }

  save(){
    if(this.type == 'BOTH')
      this.updateSchedulesBulk();
    else
      this.updateSchedule();
  }

  updateSchedule(){
    let selectedShift, object = {}, tempArr = [];
    if(this.type == 'LOGIN')
      selectedShift = this.selectedLoginShift;
    else if(this.type == 'LOGOUT')
      selectedShift = this.selectedLogoutShift;
    object['adhoc'] = false;
    object['empGuid'] = this.employeeId;
    object['gender'] = 'male';
    object['hours'] = selectedShift['hours']; 
    object['minutes'] = selectedShift['minutes'];
    object['projectId'] = this.team.projectId;
    object['scheduleDate'] = this.date.getTime();
    object['scheduleRequestType'] = this.type;
    tempArr.push(object);
    this.update.emit({
      'empGuid': this.employeeId,
      'projectId': this.team.projectId,
      'schedules': tempArr
    });
  }

  updateSchedulesBulk(){
    let object = {};
    object['dateList'] = this.selectedDates;
    object['nextDatLogOut'] = this.isSameDayLogout;
    object['logInShift'] = {
      hrs: this.selectedLoginShift['hours'],
      minutes: this.selectedLoginShift['minutes'],
      office: this.selectedLoginOffice['address']
    };
    object['logOutShift'] = {
      hrs: this.selectedLogoutShift['hours'],
      minutes: this.selectedLogoutShift['minutes'],
      office: this.selectedLogoutOffice['address']
    };
    object['projectId'] = this.team.projectId;
    this.update.emit(object);
  }

  closeEdit(){
    this.close.emit();
  }

  setSameDayLogout(event){
    this.isSameDayLogout = event.srcElement.checked;
  }

}

