import { Component, OnInit, Input, Output, EventEmitter, NgZone } from '@angular/core';
import { OnChanges, SimpleChanges } from '@angular/core/src/metadata/lifecycle_hooks';
import { HttpService } from 'src/app/shared/services/http-service.service';
declare const $: any;

@Component({
  selector: 'mis-searchEmployee',
  templateUrl: './searchEmployee.component.html',
  styleUrls: ['./searchEmployee.component.css']
})
export class SearchEmployeeComponent {

  @Output() selectedEmployeeEmitter: EventEmitter<any> = new EventEmitter<any>();

  searchedResult;
  showSearchResult;
  selectedIndex;
  selectedEmpName;
  possibleLocations;
  selectedAddress;
  selectedLocation;
  otherAddress;

  constructor(private httpService: HttpService, private zone: NgZone) {
    this.searchedResult = [];
    this.showSearchResult = false;
    this.selectedEmpName = "";
    this.possibleLocations = [];
    this.selectedAddress = 0;
    this.otherAddress = '';
    this.selectedLocation = {};
  }

  ngOnInit(){

  }

  changeAddress(index){
    this.selectedAddress = index;
  }

  searchEmployee(searchedValue){
    if(searchedValue.length > 2)
    {
      this.showSearchResult = false;
      let _t = this;
      this.httpService.searchEmployee(searchedValue).subscribe(data => {
        if(data && data['length'] != 0)
        {
          _t.searchedResult =  data;
        }
        else{
          _t.searchedResult = [{
            name: "Not present in the list"
          }];
        }
        _t.showSearchResult = true;
      }, (err) => {
        console.log(err);
      });
    }
    else
    {
      this.showSearchResult = false;
      return;
    }
  }

  selectEmployee(index){
    if((this.searchedResult.length > 1) || (this.searchedResult.length == 1 && this.searchedResult[index]['name'] != 'Not present in the list')){
      this.selectedIndex = index;
      this.selectedEmpName = this.searchedResult[index]['name'] + " (" + this.searchedResult[index]['eid'] + ")";
      if(this.searchedResult[this.selectedIndex].pickup == undefined){
        let nodalIndex = -1;
        for(let i=0; i<this.possibleLocations.length; i++){
          if(this.possibleLocations[i]['type'] == 'Nodal'){
            nodalIndex = i;
            break;
          }
        }
        this.zone.run(() => {
          this.possibleLocations.splice(nodalIndex, 1);
        });
      }
    }
    this.showSearchResult = false;
  }

  sendEmployee(){
    if(this.selectedIndex != undefined){
      this.selectedEmpName = ""; this.otherAddress = "";
      this.selectedEmployeeEmitter.emit(this.searchedResult[this.selectedIndex]);
    }
  }
}